import React, { Component, lazy, Suspense } from "react";
import type { ReactElement } from "react";
import { connect } from "react-redux";
import { isEmpty, isStaticPage, isSafari } from "fancyutils";

import { Sidebar } from "./sidebar/index";
import { historyData } from "../container/history";
import { mapStateToThingProps } from "./map";

const FooterReviews = lazy(() => import(/* webpackChunkName: "OverlayThing.extras" */ "./FooterReviews"));

import Thing from "./Thing";
import { OverlayPropsV1 } from "ftypes";
import Timeline from "./Recommendation";

import { updateSaleContext } from "../action/action-helpers";
import store from "../store/store";

function getAdminSidebar(props: OverlayPropsV1) {
    const {
        appContext: { viewer },
        thing,
    } = props;
    let admin = null;

    let AdminComponent;
    if (viewer.is_admin_business || viewer.is_admin_design) {
        AdminComponent = lazy(() => import(/* webpackChunkName: "OverlayThing.admin" */ "./Admin"));
    } else if (viewer.is_admin_wholesale && thing.show_on_homepage) {
        AdminComponent = lazy(() => import(/* webpackChunkName: "OverlayThing.admin" */ "./AdminSimple"));
    }
    if (AdminComponent) {
        admin = (
            <Suspense fallback={<div />}>
                <AdminComponent {...props} fallback={<div />} />
            </Suspense>
        );
    }

    return admin;
}

// Container class for overlayed thing detail.
class _Overlay extends Component<OverlayPropsV1> {
    // health check
    componentDidUpdate(prevProps: OverlayPropsV1) {
        const pt = prevProps.thing;
        const ct = this.props.thing;

        if (ct != null) {
            if (!pt || pt.id !== ct.id) {
                if (isStaticPage()) {
                    $(window).scrollTop(0);
                } else {
                    $("#overlay-thing > .popup").attr("tabindex", -1).focus();
                }
                if (ct.sales && ct.sales.options && ct.sales.options.length > 1) {
                    const nonSoldoutOptions = ct.sales.options.filter((o) => !o.soldout);
                    let minimumOption;
                    if (nonSoldoutOptions.length > 0) {
                        minimumOption = _.min(nonSoldoutOptions, (o) => Number(o.price));
                    } else {
                        minimumOption = _.min(ct.sales.options, (o) => Number(o.price));
                    }
                    if (ct.sales.options[0].id !== minimumOption.id) {
                        store.dispatch(updateSaleContext({ saleOptionID: minimumOption.id }));
                    }
                }
            }
        }
    }

    render() {
        const { thing } = this.props;

        let display: ReactElement | null = null;
        if (historyData.locationIsThingPage) {
            if (thing != null) {
                const showReview = thing.sales && !isEmpty(thing.sales.reviews);
                display = (
                    <div
                        className={`wrapper-content thing-detail-container thing-detail-overlay ${
                            isSafari() ? "safari" : ""
                        }`}
                        key="thing-detail-container">
                        <div className="content">
                            <Thing {...this.props} />
                            {showReview && (
                                <Suspense fallback={<div />}>
                                    <FooterReviews {...this.props} />
                                </Suspense>
                            )}
                        </div>
                        <Sidebar {...this.props} />
                        <Timeline fallback={<div className="timeline other-thing newcard loading" />} {...this.props} />
                        {getAdminSidebar(this.props)}
                        <a href="#header" id="scroll-to-top" style={{ display: "inline" }}>
                            <span>Jump to top</span>
                        </a>
                    </div>
                );
            } else {
                display = (
                    <div
                        className="wrapper-content thing-detail-container thing-detail-overlay"
                        key="thing-detail-container"
                    />
                );
            }
        } else {
            display = <div key="thing-detail-container" />;
        }

        return display;
    }
}

const ReduxComponentEnhancer = connect<OverlayPropsV1>(mapStateToThingProps);
const Overlay = ReduxComponentEnhancer(_Overlay);
export default Overlay;
